import React from 'react';
import './SideBar.css';
import { Nav } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';
class SideBarLeft extends React.Component {
    render() {
        return (
            <div className="sideBarLeft">
                <Nav className="flex-column mid">
                    <NavLink to='/AboutMe' activeClassName='active'  className='high-link nav-link'>About Me</NavLink>
                    <NavLink to='/Education' activeClassName='active'  className='high-link nav-link'>Education</NavLink>
                    <NavLink to='/Projects' activeClassName='active' className='high-link nav-link'>Projects</NavLink>
                    <NavLink to='/Resume' activeClassName='active' className='high-link nav-link'>Resume</NavLink>
                    <NavLink to='/Images' activeClassName='active'  className='high-link nav-link'>Images</NavLink>
                    <NavLink to='/Blogs' activeClassName='active'  className='high-link nav-link'>Blogs</NavLink>
                </Nav>
            </div>
        );
    }
}
export default SideBarLeft;