import React from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import Card from './Card';

class Endorsements extends React.Component {
    render() {
        return(
            <Container>
                <Row>
                    <Col>
                        <Card 
                            url = {require('../../images/kunal.jfif')} 
                            name = 'Kunal Pant' 
                            position='Computer Scientist'
                        />
                    </Col>
                    <Col>
                        <Card 
                            url = {require('../../images/hem.jfif')} 
                            name = 'Hem Pant' 
                            position='Senior Security Specilist'
                        />
                    </Col>
                    <Col>
                        <Card 
                            url = {require('../../images/ankita.jfif')} 
                            name = 'Ankita Singh' 
                            position='IAS'
                        />
                    </Col>
                    <Col>
                        <Card 
                            url = {require('../../images/divyansh.jfif')} 
                            name = 'Divyansh Agrawal' 
                            position='Data Scientist'
                        />
                    </Col>
                </Row>

            </Container>
            );
    }
}
export default Endorsements;