import React from 'react';
import {Container, Row, Col} from 'react-bootstrap';


class AboutMe extends React.Component {
    render() {
        return (
            <Container fluid className='justify-content-md-center'>
                    <Row>
                        <Col>
                            <h3 style={{padding: 20}}>About Me</h3>
                        </Col>
                    </Row>
                    <Row >
                        <Col>
                            <hr/>
                            <h5>Current: </h5>
                            <p className='about'>
                            Majors in <strong>Computer Science Engineering</strong>,  | class of 2017-2021
                            <br/><strong>Galgotias University</strong>, Greater Noida
                            <hr />
                            </p>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <h5>Address: </h5>
                            <p className='about'>
                            Ramnagar, Nainital, UK                            
                            <hr />
                            </p>
                        </Col>
                    </Row>
                </Container>
        )
    }
};
export default AboutMe;