import React from 'react';
import './DetailsPage.css';
import { Container, Row, Col} from 'react-bootstrap';
import ProfileImage from '../profileImage/ProfileImage';
import {
    Switch,
    Route,
  } from "react-router-dom";
import AboutMe from '../../pages/aboutMe/AboutMe';
import Education from '../../pages/education/Education'
import {Nav} from 'react-bootstrap';
import GitHubIcon from '@material-ui/icons/GitHub';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import Contact from '../../pages/contact/Contact';
import Projects from "../../pages/projects/Projects"

class DetailsPage extends React.Component {
    render() {
        return (
            <div className="page">
                <Container>
                    <Row className='justify-content-md-center'>
                        <Col sm={12} md={4}>
                            <ProfileImage />
                            <div className="my-intro">
                                    <h4 className='te'> Nikhil Sati</h4>
                                    <p>Software Developer at KP.inc</p>
                                </div>
                                <Col>
                                    <Nav className='navy' varient = 'success'>
                                        <Nav.Link href="https://www.linkedin.com/in/nikhil-sati-747a99106/" target="_blank">
                                            <LinkedInIcon color="primary"/>
                                        </Nav.Link>
                                        <Nav.Link href="https://github.com/Nikhilsati" target="_blank">
                                            <GitHubIcon color="primary"/>
                                        </Nav.Link>
                                        <Nav.Link href="https://www.hackerrank.com/nikhil121sati_5" target="_blank">
                                            <img width='25px' alt='hackerrank' src={require('../../images/hackerrank.png')}></img>
                                        </Nav.Link>
                                    </Nav>
                                </Col>
                        </Col>
                        <Col md={8} sm={12}>   
                            <div className='intro'>  
                                {/* <Router> */}
                                    <Switch>
                                        <Route exact path="/"/>
                                        <Route path="/AboutMe" component={AboutMe}/>
                                        <Route path="/Education" component={Education} />
                                        <Route path='/Contact' component={Contact} />
                                        <Route path='/Projects' component={Projects} />
                                    </Switch>
                                {/* </Router> */}
                            </div>
                        </Col>
                        
                    </Row>
                    <Row>
                        
                    </Row>
                </Container>
            </div>
        );
    }
}
export default DetailsPage;