import React from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import './footer.css'
export default class Footer extends React.Component {
    render() {
      return (
          <div className='foot' >
                <Container>
                    <Row>
                        <Col>
                            <p>Made with ❤ and React</p>
                        </Col>
                        <Col>
                            &copy; {new Date().getFullYear()} Copyright: <a className="link" href="https://www.MDBootstrap.com"> Nikhilsati.com </a>
                        </Col>
                    </Row>
                </Container>
          </div>
        
      );
    }
  }