import React from 'react';
import './Card.css';
import CardFront from './CardFront';
import CardBack from './CardBack';
class Card extends React.Component {
    render() {
      return(
        <div className='card-container'>
          <div className='card-body'>
            <CardBack url = {this.props.url} name = {this.props.name} position= {this.props.position}  />
            <CardFront url = {this.props.url} name = {this.props.name} position= {this.props.position}   />
          </div>
        </div>
      )
    }
  }
export default Card;