import React from 'react';
import {Nav, Navbar} from 'react-bootstrap';
import InstagramIcon from '@material-ui/icons/Instagram';
import FacebookIcon from '@material-ui/icons/Facebook';

class NavBar extends React.Component {
    render() {
        return (
                <Navbar expand="sm" sticky="top">
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="mr-auto navy">
                            <Nav.Link href="/AboutMe">Home</Nav.Link>
                            <Nav.Link href="/Contact">Contact</Nav.Link>
                        </Nav>
                        <Nav className='navy' varient = 'success'>
                            <Nav.Link href="https://www.facebook.com/nikhil.sati.5" target="_blank">
                                <FacebookIcon color="primary" />
                            </Nav.Link>
                            <Nav.Link href="https://www.instagram.com/sati.nikhil/" target="_blank">
                                <InstagramIcon color="secondary" />
                            </Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>
        );
    }
}
export default NavBar;