import React from 'react';
import './SideBar.css';
import SkillsCard from '../skillsCard/SkillsCard'
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
class SideBarRight extends React.Component {
    render() {
        return (
            <div className="sideBarRight">
                <Typography component="div">
                    <Box textAlign="center" m={1}>
                        <h3 className='heading-skills'>Skills</h3>
                    </Box>
                    </Typography>
                <SkillsCard Name="C++" Image={require('../../images/c++.svg')} value = "70" color="primary" level='Intermediate' />
                <SkillsCard Name="JavaScript" Image={require('../../images/JS.png')} value = "90" color="secondary" level='Intermediate' />
                <SkillsCard Name="Python" Image={require('../../images/python.png')} value = "60" color="primary" level='Beginner' />
                <SkillsCard Name="React" Image={require('../../images/React.png')} value = "50" color="secondary" level='Beginner' />
            </div>
        );
    }
}
export default SideBarRight;